
<template>
  <div class="p-all-12 bg-fff br-8 mb-12">
    <icon-content :src="require('@/assets/tempfile/aj/headerImage/qyjs.png')">
    </icon-content>
    <!--  -->
    <slot name="cardContent">
      <div>
        <!--  -->
        <van-image
          :src="require('@/assets/tempfile/aj/image/qyjs.png')"
        ></van-image>
      </div>
      <!--  -->
      <div
        style="
          margin-top: 12px;
          white-space: pre-line;
          font-size: 12px;
          color: #666;
        "
      >
        <div
          v-html="
            submitText(
              '<h4>&</h4>&nbsp&nbsp&nbsp&nbsp&nbsp:</br> &nbsp&nbsp&nbsp&nbsp&nbsp</br> &nbsp&nbsp&nbsp&nbsp&nbsp200+128600+100036</br>&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp:: 20+8:5000+&nbsp&nbsp&nbsp&nbsp&nbsp'
            )
          "
        ></div>
      </div>
    </slot>
  </div>
</template>

<script>
import iconContent from "../iconContent.vue";
import VueDplayer from "vue-dplayer";
export default {
  components: { iconContent, VueDplayer },
  data() {
    return {
      options: {
        video: {
          url: "https://card-img1.oss-cn-beijing.aliyuncs.com/tttx/tttx.mp4",
          pic: "https://card-img1.oss-cn-beijing.aliyuncs.com/tttx/video.png",
        },
      }, ///   https://dplayer.diygod.dev/zh/guide.html#
    };
  },
  methods: {
    submitText(text) {
      if (text) {
        let t = [];
        text
          .split("\n")
          .forEach((item) => t.push(`<p style=margin:0px;>${item.trim()}</p>`));
        return t.join("\n");
      }
    },
  },
};
</script>

<style lang="scss" scoped>
/deep/ .svgIcon {
  justify-content: center;
}
</style>



